import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    state,
} from '@sartorius-elements/base';
import { version } from './metadata';
import { loaderStyles } from './loaderStyle';
import { LoaderProperties, LoaderSlot } from './loaderProperties';

@defineElement({ tagName: 'loader', styles: loaderStyles, version })
export class Loader extends BaseElement implements LoaderProperties {
    // Static members
    private static readonly FIRST_BREAKPOINT = 400;
    private static readonly SECOND_BREAKPOINT = 1000;
    private static readonly THIRD_BREAKPOINT = 10000;
    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, converter: booleanConverter })
    loaded = false;
    @property({ type: Boolean, converter: booleanConverter })
    toggleable = false;

    private _startTime;

    @state()
    private _elapsedTime;
    @state()
    private _timerRunning;
    private _timerFunc;

    constructor() {
        super();
        this._timer = this._timer.bind(this);
    }

    protected update(changedProperties) {
        if (changedProperties.has('loaded')) {
            if (this.loaded) {
                if (this._elapsedTime > Loader.FIRST_BREAKPOINT && this._elapsedTime < Loader.SECOND_BREAKPOINT) {
                    setTimeout(() => {
                        this._timerRunning = false;
                        this._startTime = null;
                        this._dispatchEvent('loader-end');
                        clearInterval(this._timerFunc);
                    }, Loader.SECOND_BREAKPOINT - this._elapsedTime);
                } else {
                    this._timerRunning = false;
                    this._startTime = null;
                    this._dispatchEvent('loader-end');
                    clearInterval(this._timerFunc);
                }
            } else {
                this._startTime = Date.now();
                this._timerRunning = true;
                this._dispatchEvent('loader-start');
                this._timerFunc = setInterval(this._timer, 10);
            }
        }
        super.update(changedProperties);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        const actionHtml =
            this._elapsedTime < Loader.THIRD_BREAKPOINT
                ? html``
                : html`<slot class="${baseClassName}-action" name="${LoaderSlot.ACTION_SLOT}"></slot>`;
        const additionalHtml =
            this._elapsedTime < Loader.SECOND_BREAKPOINT
                ? html``
                : html`<slot
                      class="${baseClassName}-additional-info"
                      name="${LoaderSlot.ADDITIONAL_INFO_SLOT}"
                  ></slot>`;
        const contentHtml = html`<slot class="${baseClassName}-content"></slot>`;
        let loaderHtml;
        if (this.toggleable)
            loaderHtml = html`<div class="${baseClassName}-slot-wrapper">
                <slot class="${baseClassName}-loader" name="${LoaderSlot.LOADER_SLOT}"></slot
                >${additionalHtml}${actionHtml}
            </div>`;
        else {
            loaderHtml =
                this._elapsedTime > Loader.FIRST_BREAKPOINT
                    ? html`<div class="${baseClassName}-slot-wrapper">
                          <slot class="${baseClassName}-loader" name="${LoaderSlot.LOADER_SLOT}"></slot
                          >${additionalHtml}${actionHtml}
                      </div>`
                    : html``;
        }
        const slotHtml = this.loaded && !this._timerRunning ? html`${contentHtml}` : html`${loaderHtml}`;
        return html` <div class="${rootClasses}">${slotHtml}</div> `;
    }

    private _timer() {
        this._elapsedTime = Date.now() - this._startTime;
    }

    private _dispatchEvent(event) {
        const customEvent = new CustomEvent(event, {
            bubbles: false,
            composed: true,
            detail: {
                loaded: this.loaded,
            },
        });
        this.dispatchEvent(customEvent);
    }
}
